.flex {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    &--nowrap {
        flex-wrap: nowrap;
    }
    &--grow {
        flex-grow: 1;
        flex-shrink: 1;
    }
    &--vertical {
        flex-direction: column;
    }
    &--justify {
        &-center {
            justify-content: center;
        }
		&-start {
			justify-content: flex-start;
		}
		&-around {
			justify-content: space-around;
		}
		&-between {
			justify-content: space-between;
		}
    }
    &--align {
        &-top {
            align-items: top;
        }
        &-center {
            align-items: center;
        }
        &-start {
            align-items: flex-start;
        }
    }
}